<template>
    <div class="table-list-page">
        <techo-list ref="list" :title="title" :header="header" :data="data" :isShowCode="isShowCode" :disCodeOption="disCodeOption" :recordCount="recordCount" @onAdd="handleAdd"
            @onPageChange="handlePageChange" @onDelete="handleDelete" @onView="handleView" @onEdit="handleEdit" @onTreeNode="handleTreeNode">
            <div slot="header">
                <el-row type="flex" justify="space-around" class="techo-list__header-row">                    
                    <el-col :span="10">
                        <span class="techo-list__header-col-txt-4">姓名：</span>
                        <div class="techo-list__header-col-left-4">
                        <el-input placeholder="请输入姓名" :maxlength="10" size="small" class="techo-list__header-select-full"
                            clearable v-model="queryForm.name"></el-input>
                        </div>
                    </el-col>
                    <el-col :span="9">
                    </el-col>
                    <el-col :span="5" class="techo-list__header-col-button">
                        <el-button size="small" class="techo-list__header-button-reset" @click="handleClear">清空</el-button>
                        <el-button size="small" class="techo-list__header-button-query" @click="handleQuery">查询</el-button>
                    </el-col>
                </el-row>
            </div>
        </techo-list>
        <techo-details v-if="dlgVisible" ref="details" :configKey="configKey" :height="'60%'" :title="title"></techo-details>
        <vAddDialog ref="vAddDialog" :disCodeOption="disCodeOption"></vAddDialog>
    </div>
</template>

<script>
import service from "./services/special_people_service.js";
import vAddDialog from "./aids-people-add";

export default {
  components: {
    vAddDialog
  },
  data() {
    return {
      title: "艾滋病人员",
      queryForm: {
        districtCode: "",
        name: ""
      },
      header: {
        姓名: "name",
        感染途径: "dicMap.infectionWay",
        有无违法犯罪史: "dicMap.iscriminal",
        // 违法犯罪情况: "criminalSituation",
        案件类别: "dicMap.caseType",
        关注类型: "dicMap.concernedType",
        // 帮扶情况: "helpSituation",
        帮扶人姓名: "helpName",
        帮扶人联系方式: "helpContact",
        收治情况: "dicMap.treatmentSituation",
        收治机构名称: "treatmentOrgname"
      },
      data: [],
      recordCount: 0,
      disCodeOption: [],
      isShowCode: ["6102"], // 数默认展开第一级
      // 详情页面是否显示
      dlgVisible: false,
      configKey: "aidsPeople"
    };
  },
  mounted() {
    service.requestGridCode(data => {
      this.disCodeOption = [data];
      this.isShowCode = [data.code];
      this.queryForm.districtCode = data.code;
    });
  },

  watch: {
    queryForm: {
      handler(newName, oldName) {
        this.handleQuery();
      },
      deep: true
    }
  },

  methods: {
    /** 点击行政区树 返回行政区编码 */
    handleTreeNode(node) {
      this.queryForm.districtCode = node.code;
    },
    /** 添加艾滋病人员 */
    handleAdd() {
      this.$refs["vAddDialog"].dialogVisible = true;
      this.$refs["vAddDialog"].isAdd = true;
      this.$refs["vAddDialog"].title = "添加" + this.title;
    },
    /** 删除艾滋病人员 */
    handleDelete(tableSelection) {
      let that = this;
      this.$popup.showLoading("正在删除中，请稍候...");
      new Promise((resolve, reject) => {
        let count = 0;
        _.map(tableSelection, (item, index) => {
          service.requestDeleteAidsPeople(item.id, data => {
            count++;
            count == tableSelection.length && resolve();
          });
        });
      })
        .then(() => {
          that.$popup.showNotification({
            type: "success",
            message: "艾滋病人员删除成功！"
          });
          that.handleQuery();
          that.$popup.closeLoading();
        })
        .catch(() => {
          that.$popup.showNotification({
            type: "warning",
            message: "艾滋病人员删除失败！"
          });
          that.$popup.closeLoading();
        });
    },
    /** 页码改变后执行的函数 */
    handlePageChange() {
      this.data = [];
      this.handleQuery();
    },
    /** 查看信息 */
    handleView(index, row) {
      this.dlgVisible = true;
      this.$nextTick(function() {
        this.$refs["details"].data = row;
        this.$refs["details"].visible = true;
      });
    },
    /** 修改信息 */
    handleEdit(index, row) {
      this.$refs["vAddDialog"].dialogVisible = true;
      this.$refs["vAddDialog"].isAdd = false;
      this.$refs["vAddDialog"].title = "修改" + this.title;
      // 获取基本信息
      service.requestBasePeople(row.peopleId, data => {
        if (data.occupationType != null && data.occupationType != "") {
          let occType = [
            data.occupationType.substr(0, 1) + "0000",
            data.occupationType.substr(0, 3) + "00",
            data.occupationType
          ];
          data.occupationType = _.uniq(occType);
        } else {
          data.occupationType = [];
        }
        if (data.caseType != null && data.caseType != "") {
          let occType = [
            data.caseType.substr(0, 2) + "0000",
            data.caseType.substr(0, 4) + "00",
            data.caseType
          ];
          data.caseType = _.uniq(occType);
        } else {
          data.caseType = [];
        }
        this.$refs["vAddDialog"].$refs["vBaseForm"].baseForm = data;
        this.$refs["vAddDialog"].handlePosition(data);
      });
      this.$refs["vAddDialog"].dialogForm = _.cloneDeep(row);
    },
    /** 清空查询条件 */
    handleClear() {
      this.queryForm = {
        districtCode: constants.REGION.CODE,
        name: ""
      };
    },
    /** 查询列表 */
    handleQuery() {
      let pageInfo = this.$refs["list"].getPageInfo();
      pageInfo = { ...pageInfo, ..._.cloneDeep(this.queryForm) };
      service.requestPageAidsPeople(pageInfo, data => {
        if (data.totalCount != 0 && data.list.length == 0) {
          pageInfo.page = data.totalPage;
          service.requestPageAidsPeople(pageInfo, data => {
            this.recordCount = data.totalCount;
            _.map(data.list, item => {
              if (item.dicMap) {
                _.map(data.list, item => {
                  item.dicMap &&
                    (item.dicMap.iscriminal =
                      item.iscriminal == 1 ? "有" : "无");
                });
              }
            });
            this.data = data.list;
          });
        } else {
          this.recordCount = data.totalCount;
          _.map(data.list, item => {
            if (item.dicMap) {
              _.map(data.list, item => {
                item.dicMap &&
                  (item.dicMap.iscriminal = item.iscriminal == 1 ? "有" : "无");
              });
            }
          });
          this.data = data.list;
        }
      });
    }
  }
};
</script>
